.burger {
  position: relative;
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-right: 20px;
  display: none;
  span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 20px;
    height: 2px;
    border-radius: 2px;
    background-color: $black;
    transition: all 0.3s;
    &:nth-of-type(1) {
      position: absolute;
      content: '';
      width: 20px;
      height: 2px;
      border-radius: 2px;
      background-color: $black;
      top: calc(50% - 5px);
    }
    &:nth-of-type(2) {
      position: absolute;
      content: '';
      width: 20px;
      height: 2px;
      border-radius: 2px;
      background-color: $black;
      top: calc(50% + 5px);
    }
  }
}

.burger.active span:nth-of-type(1) {
  display: none;
}
.burger.active span:nth-of-type(2) {
  top: 50%;
  transform: translate(-50%, 0%) rotate(45deg);
}
.burger.active span:nth-of-type(3) {
  top: 50%;
  transform: translate(-50%, 0%) rotate(-45deg);
}

@media (max-width: 960px) {
  .burger {
    display: inline-flex;
  }
}
