.post__card {
  padding: 19px;
  background-color: $accent;
  border: 1px solid $border;
  .icon {
    width: 18px;
    height: 18px;
    fill: $grayText;
  }
  .post__title {
    display: block;
    cursor: pointer;
    &:hover {
      color: $darkBlue;
    }
  }
  .post__img {
    width: 100%;
    height: 470px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
.post__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  align-items: center;
}
.post__header-left {
  display: flex;
  align-items: center;
  .user__avatar {
    margin-right: 8px;
  }
  .user__name {
    margin-right: 12px;
  }
}
.user__avatar {
  width: 24px;
  height: 24px;
}

.meatballs {
  width: 24px;
  height: 24px;
  display: flex;
  position: relative;
  cursor: pointer;
  .icon {
    width: 100%;
    height: 100%;
    fill: $white;
  }
}
.post__main {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 15px;
}
.post__content {
  color: #000f13;
  text-align: left;
  overflow: hidden;
  position: relative;
  .read__full {
    height: 53px;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    background: linear-gradient(
      180deg,
      rgba(250, 254, 255, 0) 0%,
      #fafeff 43.23%
    );
    display: flex;
    align-items: flex-end;
  }
}

.post__bottom {
  padding-top: 15px;
  border-top: 1px solid $gray;
}
.post__tags {
  margin-bottom: 15px;
}
.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.tag {
  padding: 5px 7px;
  background: transparent;
  border: 1px solid $grayText;
  color: $grayText;
}
.post__actions {
  display: flex;
  align-items: center;
}
.post__actions-left {
  display: flex;
  align-items: center;
  gap: 20px;
}
.post__actions-left-item {
  display: flex;
  align-items: center;
  gap: 6px;
}
.post__actions-right {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-left: auto;
}
.post__pins.post__actions-left-item.active {
  .icon {
    fill: $yellow;
  }
}
.post__actions-right-item {
  display: flex;
  align-items: center;
  color: $grayText;
  gap: 6px;
}

.post__smile-sad.active {
  .icon {
    fill: $red;
  }
}

.post {
  .card__ad {
    display: none;
  }
  .card__immigrant {
    display: none;
  }
}

.ad__card {
  .post__content {
    margin-bottom: 15px;
  }
  .card__post {
    display: none;
  }
  .card__immigrant {
    display: none;
  }
}

.immigrant__card {
  .card__post {
    display: none;
  }
  .card__ad {
    display: none;
  }
}

@media (max-width: 1200px) {
  .post__card {
    .post__img {
      height: 340px;
    }
  }
}
